"use client"

import { useEffect, useState } from "react"
import { useParams, useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import CommentsSection from "@/components/comments-section"
import ReactionsBar from "@/components/reactions-bar"
import ReviewsSection from "@/components/reviews-section"
import PdfViewer from "@/components/pdf-viewer" // Import the new PdfViewer component

interface Book {
  _id: string
  title: string
  description: string
  price: number
  type: string
  cover: string
  isbn: string
  filePath?: string
  stock?: number
}

export default function BookDetailPage() {
  const params = useParams()
  const router = useRouter()
  const id = params.id as string
  const [book, setBook] = useState<Book | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    async function fetchBook() {
      try {
        const res = await fetch(`/api/books/${id}`)
        if (res.ok) {
          const data = await res.json()
          setBook(data)
        }
      } catch (error) {
        console.error("Failed to fetch book:", error)
      } finally {
        setLoading(false)
      }
    }
    fetchBook()
  }, [id])

  const handleAddToCart = async () => {
    if (!book) return
    try {
      const res = await fetch("/api/cart", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          bookId: id,
          quantity: 1,
          price: book.price,
        }),
      })
      if (res.ok) {
        alert("Added to cart!")
        router.push("/cart")
      } else {
        alert("Please login to add items to cart")
      }
    } catch (error) {
      console.error("Failed to add to cart:", error)
    }
  }

  if (loading) return <div className="py-12 text-center">Loading...</div>
  if (!book) return <div className="py-12 text-center">Book not found</div>

  return (
    <main className="min-h-screen bg-background py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-4xl mx-auto">
        {/* Book Header */}
        <div className="grid md:grid-cols-2 gap-8 mb-12">
          <div className="aspect-video bg-muted rounded-lg overflow-hidden">
            {book.cover ? (
              <img src={book.cover || "/placeholder.svg"} alt={book.title} className="w-full h-full object-cover" />
            ) : (
              <div className="w-full h-full flex items-center justify-center bg-primary/10">
                <span className="text-muted-foreground">No Cover</span>
              </div>
            )}
          </div>

          <div className="flex flex-col justify-between">
            <div>
              <h1 className="text-4xl font-bold text-foreground mb-4">{book.title}</h1>
              <p className="text-lg text-muted-foreground mb-6">{book.description}</p>

              <div className="space-y-3 mb-6">
                <p className="text-sm text-muted-foreground">ISBN: {book.isbn}</p>
                {book.stock !== undefined && <p className="text-sm text-muted-foreground">Stock: {book.stock}</p>}
              </div>
            </div>

            <div className="space-y-4">
              <div className="text-3xl font-bold text-primary">${book.price}</div>
              <div className="flex gap-4">
                <Button onClick={handleAddToCart} className="flex-1">
                  Add to Cart
                </Button>
                {/* The "Read Now" button is replaced by the embedded viewer below */}
              </div>
            </div>
          </div>
        </div>

        {/* Book Content Area */}
        <div className="bg-card p-8 rounded-lg border border-border mb-8">
          <h2 className="text-2xl font-bold text-foreground mb-4">About this book</h2>
          <p className="text-foreground leading-relaxed">{book.description}</p>
        </div>

        {/* Embedded PDF Viewer */}
        {book.type !== "physical" && book.filePath && (
          <div className="mb-8">
            <h2 className="text-2xl font-bold text-foreground mb-4">Read E-book</h2>
            <PdfViewer fileUrl={book.filePath} />
          </div>
        )}

        {/* Interactions */}
        <ReactionsBar contentId={id} contentType="book" />

        {/* Reviews Section */}
        <ReviewsSection contentId={id} contentType="book" />

        {/* Comments Section */}
        <CommentsSection contentId={id} contentType="book" />
      </div>
    </main>
  )
}
